.section-1 {
    
    
    height: 100%;
    width: 100%;
    position: relative;
    
    &-container {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        
    }
    &-img{
        position: absolute;
        object-fit: cover;
        width: 100%;
        height: 100%;
    }

    &.fadeUppText{
        .intro-slider{
            &__descr{
                opacity: 1;
                transform: translateY(0) !important;
            }
        }
    }
    .intro-slider{
        &__contnet {
            margin-top: auto;
        }
        &__descr{
            transition: 0.6s ease;
            color: #fff;
            font-size:  var(--fz-xxl);
            margin-bottom: 100px;
            position: relative;
            line-height: 1.1;
            opacity: 0;
            transition: 0.7s ease-in-out;
            transform: translateY(50px);
            // &.va{
            //     opacity: 1;
            //     transform: translateY(0);
            // }
            &::after{
                content: '';
                position: absolute;
                top: -55px;
                left: 0;
                border: 1px solid var(--c-primary);
                width: 80px;
            }            
        }
        &__list{
            display: flex;
            
            
        }
        &-item-container {
            z-index: 1;
            display: flex;
        }
        &-item{
            display: flex;
            align-items: center;
            transition: background 0.3s ease-in, color 0.3s ease-in;
            z-index: 1;
            // max-width: 310px;
            
            
            &:not(:last-child){
                margin-right: 70px;
            }
            &.active{
                cursor: pointer;
                background-color: var(--c-secondary);
                width: 300px;
                a{
                    padding: 30px 35px;
                    
                }
                i {
                    display: none;
                }
                p{
                    font-size: var(--fz-s);
                    font-weight: bold;
                }
            }
            p {
                font-size: var(--fz-xs);
                font-weight: normal;
            }
        }
        &-link{
            display: flex;
            align-items: center;
            column-gap:10px ;
            width: 100%;
            i{
                color: var(--c-primary);
            }
            span{
                font-weight: 700;
                font-size:  var(--fz-s);
                text-align: center;
                transition: color 0.3s ease;
            }
            
        }
    }
}


